.App {
  text-align: center;
}
.todo__ul{
  list-style-type: none !important;
  padding: 0;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}
.App-header {
  background-color: #282c34;
  min-height: 10vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.todo__ul li {
  display: flex;
  align-items: center;
  width: 23%;
  height: 150px;
  background: #eee;
  margin: 0 30px 19px;
  padding: 20px;
  flex-wrap: wrap;
}

.todo__li__span {
  display: flex;
  width: 100%;
}

.todo__main__div{
  width: 90%;
  margin: 30px auto;
}

.App-link {
  color: #61dafb;
}

.todo__Form form{
  display: flex;
  justify-content: space-between;
}


/* Modal */
.app__todo__modal{
  width: 70%;
  background-color: #fff;
  margin: 13vh auto;
  height: 50vh;
}

.app__todo__form{
  height: 100%;
  background-color: #fff;
  margin: 0 auto;
}

span.atp__title__label {
  font-size: 22px;
  color: #000;
  width: 20%;
  text-align: center;
}

.atp__title,.apt__desc {
  flex: 1;
}

textarea.atp__desc {
  flex: 1;
}
span.atp__span {
  display: flex;
  padding: 10px;
  flex-wrap: nowrap;
}
/* Modal */

/* Todo List  */
span.urgent {
  display: flex;
  align-items: center;
  font-size: 12px;
  font-weight: 600;
  color: #f50057;
}

span.date__prio {
  margin-right: 1em;
  display: flex;
  align-items: center;
  font-size: 12px;
    font-weight: bold;
}
span.date__prio__main {
  display: flex;
  align-items: center;
  align-content: center;
  width: 100%;
}

span.edit__delete__main {
  display: flex;
}
/* Todo List  */


